import { Layout, Row, Col, List, Button } from "antd";
import { Head, Foot } from "../components/HeadFoot";
import { Link } from "react-router-dom";
import "antd/dist/antd.css";
import { Component } from "react";
import CryptoJS from "crypto-js";
const { Content } = Layout;
class ListPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: this.props.match.params.name,
      // referer: this.props.location.state.referer,
      list: "",
      data: [],
    };
  }
  render() {
    return (
      <Layout>
        <Head />
        <Content>
          <Row>
            <Col
              xs={{ span: 24, offset: 0 }}
              sm={{ span: 24, offset: 0 }}
              md={{ span: 24, offset: 0 }}
              xl={{ span: 18, offset: 3 }}
            >
              <List
                size="large"
                bordered
                dataSource={this.state.data}
                renderItem={(item) => (
                  <List.Item>
                    <Link
                      to={{
                        pathname: `/player/${item.name}`,
                        state: {
                          referer: this.props.location.pathname,
                          name: item.name,
                          url: (() => {
                            let urlDecrypt = CryptoJS.AES.decrypt(
                              item.urlEncrypt
                                .replace(/\r/g, "")
                                .replace(/\n/g, ""),
                              "54520"
                            ).toString(CryptoJS.enc.Utf8);
                            let blob = new Blob([urlDecrypt], {
                              type: "application/vnd.apple.mpegURL",
                            });
                            let url = URL.createObjectURL(blob);
                            return url;
                          })(),
                        },
                      }}
                    >
                      {item.name}
                    </Link>
                  </List.Item>
                )}
              />
            </Col>
          </Row>
          <Row>
            <Col
              xs={{ span: 24, offset: 0 }}
              sm={{ span: 24, offset: 0 }}
              md={{ span: 24, offset: 0 }}
              xl={{ span: 18, offset: 3 }}
            >
              <Button shape="round" onClick={() => this.props.history.goBack()}>
                ËøîÂõû
              </Button>
            </Col>
          </Row>
        </Content>
        <Foot />
      </Layout>
    );
  }
  componentWillMount() {
    try {
      fetch("https://cdn.jsdelivr.net/gh/ssyatelandisi/ssyatelandisi@master/docs/data.json")
        .then((res) => res.json())
        .then((res) => {
          for (let l of res) {
            if (l.name === this.props.match.params.name) {
              this.setState({
                list: l.name,
                data: l.list,
              });
            }
          }
        });
    } catch (error) {}
  }
  componentDidMount() {
    document.title = this.state.title;
  }
}
export default ListPage;
